#card {
  color: white;
  margin: 1em;
  display: flex;
  justify-content: space-between;
  background-color: #ED6112;
  border-radius: 15px;
  /* box-shadow: 0 5px 15px rgba(4, 0, 255, 0.609) */
}

#card a {
  background: none;
	color: #01002E;
	border: none;
	font: inherit;
	cursor: pointer;
	outline: inherit;
  text-decoration: none;
}

#card a h3 {
  text-align: center;
  margin: 0.5em;
}

#card a p {
  text-align: justify;
  margin: 0.5em;
}

#card a img {
  border-radius: 15px 15px 0 0;
  height: 30vh;
  width: 30vh;
}
